.deleteBackground {
  cursor: pointer;
  fill: rgba(201, 160, 160, 0.2);
}

.line {
  cursor: pointer;
  stroke-dashoffset: 1000;
  animation: dash linear infinite;
  animation-duration: 0s;
}

@keyframes dash {
  from {
    stroke-dashoffset: 1000;
  }
  to {
    stroke-dashoffset: 0;
  }
}